@import '../bower_components/normalize-css/normalize.css';
@import url("https://fonts.googleapis.com/css?family=Quattrocento+Sans:400,700");
body,
html {
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Helvetica", sans-serif;
  background: #467820;
}
.Container {
  *zoom: 1;
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}
.Container:before{
  content: '';
  display: table;
}
.Container:after{
  content: '';
  display: table;
  clear: both;
}
.Wrapper {
  *zoom: 1;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
  background: #f9f8f3;
  margin-top: -90px;
  padding: 40px 20px;
  box-sizing: border-box;
}
.Wrapper:before{
  content: '';
  display: table;
}
.Wrapper:after{
  content: '';
  display: table;
  clear: both;
}
.hidden {
  display: none !important;
}
.disabled {
  opacity: 0.5 !important;
  cursor: not-allowed !important;
}
.clear {
  clear: both;
}
.ButtonSM__choose {
  background: url("../img/map-buttons-sm.png");
  background-size: 126px 116px;
  width: 31px;
  height: 29px;
  display: inline-block;
  text-indent: -9999px;
  border: none;
  background-position: -62px -87px;
}
.ButtonSM__choose:hover {
  background-position: -62px 0px;
}
.ButtonSM__choose:active {
  background-position: -62px -29px;
}
.ButtonSM__complete {
  background: url("../img/map-buttons-sm.png");
  background-size: 126px 116px;
  width: 31px;
  height: 29px;
  display: inline-block;
  text-indent: -9999px;
  border: none;
  background-position: 0px -87px;
}
.ButtonSM__complete:hover {
  background-position: 0px 0px;
}
.ButtonSM__complete:active {
  background-position: 0px -29px;
}
.ButtonSM__dismiss {
  background: url("../img/map-buttons-sm.png");
  background-size: 126px 116px;
  width: 31px;
  height: 29px;
  display: inline-block;
  text-indent: -9999px;
  border: none;
  background-position: -93px -87px;
}
.ButtonSM__dismiss:hover {
  background-position: -93px 0px;
}
.ButtonSM__dismiss:active {
  background-position: -93px -29px;
}
.Navigation {
  background: #467820;
  color: #fff;
}
.Navigation .Container {
  height: 64px;
  display: flex;
  align-items: center;
}
.Navigation a {
  color: #fff;
  text-decoration: none;
}
.Navigation__brand {
  flex: 1;
}
.Navigation__brand img {
  max-width: 157px;
  width: 100%;
}
.Navigation__links {
  flex: 2;
}
.Navigation__links ul {
  display: flex;
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.Navigation__links li {
  flex: 1;
  text-align: center;
}
.Navigation__links li.brand-mark {
  flex: 3;
}
.Navigation__links li.account {
  flex: 3;
}
.Navigation__account {
  flex: 1;
  text-align: right;
}
@media (max-width: 1100px) {
  .Navigation {
    padding: 0 20px;
  }
}
.Banner {
  height: 200px;
  background: #f2f2f2;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}
body.home .Banner {
  height: 400px;
  align-content: center;
}
body.home .Banner__headline {
  width: calc(99.99% * 1/2 - (30px - 30px * 1/2));
  height: 100%;
}
body.home .Banner__headline:nth-child(n){
  float: left;
  margin-right: 30px;
  clear: none;
}
body.home .Banner__headline:last-child{
  margin-right: 0;
}
body.home .Banner__headline:nth-child(2n){
  margin-right: 0;
}
body.home .Banner__headline:nth-child(2n + 1){
  clear: left;
}
.Banner__headline h1 {
  font-weight: 200;
}
.Banner__headline p {
  font-size: 1.25em;
  line-height: 1.5;
  font-weight: 200;
}
@media (max-width: 1100px) {
  .Banner__headline {
    box-sizing: border-box;
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .Banner {
    height: inherit;
  }
  body.home .Banner {
    height: inherit;
  }
  body.home .Banner__headline {
    width: calc(99.99% * 1 - (30px - 30px * 1));
  }
  body.home .Banner__headline:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
  }
  body.home .Banner__headline:last-child{
    margin-right: 0;
  }
  body.home .Banner__headline:nth-child(undefinedn){
    margin-right: 0;
  }
  body.home .Banner__headline:nth-child(undefinedn + 1){
    clear: left;
  }
}
form > div {
  width: calc(99.99% * 1 - (30px - 30px * 1));
  height: calc(99.99% * 1 - (30px - 30px * 1));
}
form > div:nth-child(n){
  float: left;
  margin-right: 30px;
  margin-bottom: 30px;
  clear: none;
}
form > div:last-child{
  margin-right: 0;
  margin-bottom: 0;
}
form > div:nth-child(undefinedn){
  margin-right: 0;
}
form > div:nth-child(undefinedn + 1){
  clear: left;
}
form > div:nth-last-child(-n + undefined){
  margin-bottom: 0;
}
form label {
  display: block;
  font-family: 'Quattrocento Sans';
  font-size: 0.75em;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 2;
  color: #777;
}
form input[type="text"],
form input[type="email"],
form input[type="password"] {
  border-style: none;
  border-bottom: 2px solid #ccc;
  background: #eee;
  height: 40px;
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 0.85em;
  box-sizing: border-box;
  padding: 0 10px;
}
form [type="submit"] {
  display: inline-block;
  background: #fff;
  border: 2px solid #467820;
  color: #467820;
  font-weight: 500;
  font-size: 1.25em;
  border-radius: 3px;
  padding: 10px 20px;
}
.Login {
  background: #fff;
  box-sizing: border-box;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
}
.Banner .Login {
  width: calc(99.99% * 1/2 - (30px - 30px * 1/2));
}
.Banner .Login:nth-child(n){
  float: left;
  margin-right: 30px;
  clear: none;
}
.Banner .Login:last-child{
  margin-right: 0;
}
.Banner .Login:nth-child(2n){
  margin-right: 0;
}
.Banner .Login:nth-child(2n + 1){
  clear: left;
}
.Login button[type="submit"] {
  background: #fff;
  border: 2px solid #467820;
  color: #467820;
  padding: 10px 20px;
  border-radius: 5px;
}
.Login button[type="submit"] span {
  transition: all 0.2s ease-in-out;
}
.Login button[type="submit"] span.icon {
  display: inline-block;
  overflow: hidden;
  width: inherit;
}
.Login button[type="submit"] span.text {
  display: inline-block;
  overflow: hidden;
  width: 0px;
}
.Login button[type="submit"]:hover {
  background: #a1ca81;
  border: 2px solid #a1ca81;
  color: #38601a;
  padding: 10px 20px;
  transition: all 0.2s ease-in-out;
}
.Login button[type="submit"]:hover span {
  transition: all 0.2s ease-in-out;
}
.Login button[type="submit"]:hover span.icon {
  display: inline-block;
  overflow: hidden;
  width: 0px;
}
.Login button[type="submit"]:hover span.text {
  display: inline-block;
  overflow: hidden;
  width: inherit;
}
@media (max-width: 640px) {
  .Banner .Login {
    width: calc(99.99% * 1 - (30px - 30px * 1));
  }
  .Banner .Login:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
  }
  .Banner .Login:last-child{
    margin-right: 0;
  }
  .Banner .Login:nth-child(undefinedn){
    margin-right: 0;
  }
  .Banner .Login:nth-child(undefinedn + 1){
    clear: left;
  }
}
.Actions {
  list-style-type: none;
  padding: 0;
  margin: 0;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.15);
}
.page-wrapper .Actions.centered {
  *zoom: 1;
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;
}
.page-wrapper .Actions.centered:before{
  content: '';
  display: table;
}
.page-wrapper .Actions.centered:after{
  content: '';
  display: table;
  clear: both;
}
.Actions__listItem {
  border: 1px solid #f0ebd1;
  display: flex;
  height: 54px;
  align-items: center;
  padding: 0px;
  box-sizing: border-box;
  background: #fff;
}
.Actions__listItemHeading {
  font-weight: 200;
  font-size: 1.125em;
  flex: 2;
  text-transform: capitalize;
}
.Actions__actions {
  padding: 0px 10px;
  margin: 10px 0 -10px 0;
  line-height: 3;
  display: flex;
  text-align: left;
}
.Modal {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0,0,0,0.6);
  top: 0;
  transition: all 0.5s ease-in;
  display: none;
}
.Modal__content {
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
}
.Modal__heading {
  background: #f2f2f2;
  display: block;
  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
  font-size: 1.5em;
}
.Modal__body {
  display: block;
  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}
.Modal__footer {
  background: #f2f2f2;
  display: block;
  width: 100%;
  padding: 10px 20px;
  text-align: right;
  box-sizing: border-box;
}
